/* Delivery Dashboard custom styles */
.well {
  padding: 15px;
}

.well p {
  margin-top: 10px;
}

.highlight {
  background-color: lightblue;
  color: #000;
  padding: .2em .3em 0;
  border-radius: 2px;
}

.loader {
  border: 16px solid #999;
  border-top: 16px solid #666;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  margin: 50px auto;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Releases status list table */
td {
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  max-width: 65px;
  text-align: center;
  padding-bottom: 50px !important;
}

/* Search form */
.search-form .form-group {
  margin-bottom: 0;
}

.clearable-text {
  width: 100%;
}
.clearable-text input {
  width: 100%;
  padding-right: 18px;
}

.text-clear-btn {
  position: absolute;
  right: 5px;
  top: 0;
  bottom: 0;
  height: 14px;
  margin: auto;
  font-size: 14px;
  cursor: pointer;
  color: #ccc;
}

/* Firefox releases menu */
.firefox-releases-menu button {
  padding: 0;
}



